import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: () => import('@/views/MainPage.vue'),
  },
  {
    path: '/edit/:noteId/',
    component: () => import('@/views/EditNotePage.vue'),
    props: true,
  },
  {
    path: '*',
    redirect: '/',
  }
];

export default new VueRouter({
  routes,
  mode: 'history',
});
